.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
  }
  
  .modal {
    position: relative;
    max-width: calc(100vw - 48px);
    max-height: calc(100vh - 24px);
    background-color: #fff;
    padding: 16px;
  }

  .btn-close{
    position: absolute;
    top: 8px;
    right: 8px;
    padding: 8px;
    width: 40px;
    height: 40px;
  }