.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
}

.avatar {
    border-radius: 4px;
    padding: 4px;
    background-color: #A8A8A8;
}

.contact-form {
    display: flex;
    flex-direction: column;
    gap: 24px;

    width: 288px;
    padding: 8px;
    border: 1px solid;
    border-radius: 4px;
}

.form-title {
    text-align: center;
    font-size: 24px;
    font-weight: 500;
}

.buttons-bar {
    display: flex;
    justify-content: space-around;
}

button.disabled {
    pointer-events: none;
}